import React from "react";
import {Navigate, useLocation} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {selectToken, selectUserId, tryLogOutAsync} from "../../Redux/AuthReducer";
import LoadingPage from "../LoadingPage/LoadingPage";
import {getTokenFromCookie} from "../../Cookie/AuthWithCookie";


//        
//     
const withAuthRedirect = (Component) => {
	return (props) => {
		const userId = useSelector(selectUserId);
		const token = useSelector(selectToken);
		const dispatch = useDispatch();
		const location = useLocation().pathname.split('/')[1]; //   url

		// ,     
		if(getTokenFromCookie() !== token) {
			dispatch(tryLogOutAsync()); //  ,      logout
			return <Navigate to={"/login"}/> //    
		}

		//   ,    ,          (    )
		if (token && !userId) {
			return <LoadingPage/>
		}

		//           
		//           ,   
		//    ,     
		if (location === "login") {
			if (userId)
				return <Navigate to={"/"}/>
			else
				return <Component {...props}/>
		}

		//   ,    ,     
		if (userId)
			return <Component {...props}/>
		return <Navigate to={"/login"}/>
	}
}

export default withAuthRedirect;